					</div>
				</div>
			</div>
			<div class="btn-price-data-right">
				<div>(24h) : 
                    <span v-if="btn_price_data.high != ''">{{btn_price_data.high | toFixedNum(2)}}</span>
                    <span v-if="btn_price_data.high == ''">--</span>
                </div>
				<div>(24h) : 
                    <span v-if="btn_price_data.low != ''">{{btn_price_data.low | toFixedNum(2)}}</span>
                    <span v-if="btn_price_data.low == ''">--</span>
                </div>
				<!-- <div>(24h) : 
                    <span v-if="btn_price_data.vol != ''">{{btn_price_data.vol | toFixedNum(2)}}</span>
                    <span v-if="btn_price_data.vol == ''">--</span>
                </div> -->
			</div>
		</div>
        <!--  -->
		<div class="type-box">
            <div class="type-box-line1">
                <span :class="{'active':typeIndex == 0}" @click="changeType(0)"></span>
                <span :class="{'active':typeIndex == 1}" @click="changeType(1)">1</span>
                <span :class="{'active':typeIndex == 2}" @click="changeType(2)">5</span>
                <span :class="{'active':typeIndex == 3}" @click="changeType(3)">15</span>
                <span :class="{'active':typeIndex == 4}" @click="changeType(4)">30</span>
               <div class="type-box-more" :class="{'type-box-more-active':showTypeMore || typeIndex > 4}" @click="typeMore">
                   <div v-if="typeIndex <= 4"></div>
                   <div v-if="typeIndex == 5">1</div>
                   <div v-if="typeIndex == 6">4</div>
                   <div v-if="typeIndex == 7">1</div>
                   <div v-if="typeIndex == 8">1</div>
                   <div class="type-box-more-icon" :class="{'type-box-more-icon-active':showTypeMore || typeIndex > 4}"></div>
               </div>
            </div>
            <div class="type-box-line2" v-if="showTypeMore">
                <span :class="{'active':typeIndex == 5}" @click="changeType(5)">1</span>
                <span :class="{'active':typeIndex == 6}" @click="changeType(6)">4</span>
                <span :class="{'active':typeIndex == 7}" @click="changeType(7)">1</span>
                <span :class="{'active':typeIndex == 8}" @click="changeType(8)">1</span>
            </div>
			
			
		</div>
		<!--  -->
		<div class="btn-k-data-item">
			<div>=<span :class="{'price-down':btn_k_data_item.open > btn_k_data_item.close}">{{btn_k_data_item.open}}</span></div>
			<div>=<span :class="{'price-down':btn_k_data_item.open > btn_k_data_item.close}">{{btn_k_data_item.close}}</span></div>
			<div>=<span :class="{'price-down':btn_k_data_item.open > btn_k_data_item.close}">{{btn_k_data_item.lowest}}</span></div>
			<div>=<span :class="{'price-down':btn_k_data_item.open > btn_k_data_item.close}">{{btn_k_data_item.highest}}</span></div>
		</div>
		
		<div class="echarts-box" ref="echartsBox"></div>

        <!-- <div class="btn-text-box">
			<div></div>
			<div>BTC</div>
			<div class="btn-text-item">
				<div></div>
				<div>2008-11-1</div>
			</div>
			<div class="btn-text-item">
				<div></div>
				<div>2100</div>
			</div>
			<div class="btn-text-item">
				<div></div>
				<div>81.79%</div>
			</div>
			<div class="btn-text-item">
				<div></div>
				<div>-</div>
			</div>
			<div class="btn-text-item">
				<div></div>
				<div>https://bitcoin.org/bitcoin.pdf</div>
			</div>
			<div class="btn-text-item">
				<div></div>
				<div>https://bitcoin.org/en/</div>
			</div>
			<div></div>
			<div class="btn-text-detail">
				BitcoinBTC200913P2P2100102140P2P
			</div>
        </div> -->
    </div>
</template>

<script>

import echarts from 'echarts'

export default {
    name: 'echartsDemo',
    props: {
        // btn_price_data_before:String,
        father_data:{
            type:Object
        }
    },
    data(){
        return{
            
            typeIndex:1,
            showTypeMore:false,
			btn_price_data_interval:null,
			btn_k_interval:null,
			//
			btn_price_data:{
				vol:'',//
				high:'',//
				low:'',//
				sell:'',//
				last:'',//
				buy:''//
			},
			//
			// btn_price_data_before:{

			// 	sell:'5321',//
				
			// },

			//k
			btn_k_data_item:{
				open:'4983.64',
				close:'4985.88',
				lowest:'4983.44',
				highest:'4987.69'
			},
            firstInAxisPointer:false,
            
            showClose:false
        }
    },
    filters:{
		//
		toFixedNum(val,num){
			if(val){
				return parseFloat(val).toFixed(num);
			}else{
				return ''
			}
			return val
		}
	},
	computed:{
		change_count(){
			if(this.btn_price_data.sell != '' && this.father_data.btc_price != ''){
				let now = parseFloat(this.btn_price_data.sell);
				let before = parseFloat(this.father_data.btc_price);
				return (now - before).toFixed(2);
			}else{
				return 0
			}	
		},	
		change_persent(){
			if(this.btn_price_data.sell != '' && this.father_data.btc_price != ''){
				let now = parseFloat(this.btn_price_data.sell);
				let before = parseFloat(this.father_data.btc_price);
				let change = (now - before).toFixed(2)
				return ((change/now)*100).toFixed(2);	
			}else{
				return 0
			}	
		}
	},
    created(){
        
	},
	beforeDestroy(){
		this.utils.info('...');
		if(this.btn_price_data_interval){
			clearInterval(this.btn_price_data_interval);
		}	
		if(this.btn_k_interval){
			clearInterval(this.btn_k_interval);
		}
	},
    mounted(){
        //k
		if(this.chartline){
			this.chartline.clear();
		}else{
			this.chartline = echarts.init(this.$refs.echartsBox);
		}

		//
		this.get_price_data();


		//k
		this.get_k_data(1);

		
		
		//
		if(this.btn_price_data_interval){
			clearInterval(this.btn_price_data_interval);
		}
		this.btn_price_data_interval = setInterval(() =>{
			
			//
			this.get_price_data();	
		},1000)

		//k
		if(this.btn_k_interval){
			clearInterval(this.btn_k_interval);
		}
		this.btn_k_interval = setInterval(() =>{
			//k
			this.get_k_data(2);
		},30000)
    },
    methods:{
        //
        sendToParent(){
            //this.utils.info('...');
            this.$emit('fromChild',this.btn_price_data)
        },
        //
        showCloseInfo(){
            this.showClose = !this.showClose;
        },
        //k
		changeType(index){
			if(this.typeIndex == index){
				return false;
			}
			this.typeIndex = index;
            this.firstInAxisPointer = false;
            this.showTypeMore = false;
			if(this.chartline){
				this.chartline.clear();
			}
			this.get_k_data(1);
        },
        //
        typeMore(){
            this.showTypeMore = !this.showTypeMore;
        },
		//k type 1 2
		get_k_data(flag){
			let type = '';
			let kid = 0;
			switch (this.typeIndex) {
				case 0: 
					type = "1min";
					kid = 1;
					break;
				case 1: 
					type = "1min";
					kid = 1;
					break;
				case 2: 
					type = "5min";
					kid = 2;
					break;
				case 3: 
					type = "15min";
					kid = 3;
					break;
				case 4: 
					type = "30min";
					kid = 4;
					break;
				case 5: 
					type = "1hour";
					kid = 7;
					break;
				case 6: 
					type = "4hour";
					kid = 8;
					break;
				case 7: 
					type = "1day";
					kid = 5;
					break;
				case 8: 
					type = "1week";
					kid = 6;
					break;
			}
			if(this.chartline && flag && flag == 1){
				this.chartline.showLoading({
					type:'default',
					opts:{
						text: 'loading',
						color: '#c23531',
						textColor: '#000',
						maskColor: 'rgba(255, 255, 255, 0.8)',
						zlevel: 0
					}
				});
			}
			
			
			let option = {
				type:kid
			}
			this.http.post(this.http._HOST + 'notoken/k_line',option).then(result =>{
				this.utils.info('k...111',result);	
				if(result.success && result.data.data && result.data.data.length > 0){
					let btn_data = result.data.data;
					let btn_data_tmp = [];	

					for(let i=0;i<btn_data.length;i++){
						let item = [];
						if(this.typeIndex == 7 || this.typeIndex == 8){
							item.push(this.timeFormat( btn_data[i][0],'ymd'));
						}else{
							item.push(this.timeFormat( btn_data[i][0],'hm'));
						}
						
						//item.push(btn_data[i][0]);
						item.push(btn_data[i][1]);
						item.push(btn_data[i][4]);
						item.push(btn_data[i][3]);
						item.push(btn_data[i][2]);
						item.push(btn_data[i][5]);
						
						btn_data_tmp.push(item);
					}

					//
					this.gotoecharts(btn_data_tmp);

					
				}else{
					this.utils.err('k...err...3s');

					let getKDataTimeout = setTimeout(() =>{
						this.get_k_data(flag);
						clearTimeout(getKDataTimeout);	
					},3000)	
				}
				
			})

			//let url = 'http://api.zb.cn/data/v1/kline?market=btc_usdt&type=' + type + '&size=100';
			//let url = 'http://api.bitkk.com/data/v1/kline?market=btc_usdt&type=1min&size=40';
			// this.$jsonp(url).then(result =>{
			// 	//this.utils.info('k...',result);
			// 	let btn_data = result.data;	
			// 	let btn_data_tmp = [];

			// 	for(let i=0;i<btn_data.length;i++){
			// 		let item = [];
			// 		if(this.typeIndex == 7 || this.typeIndex == 8){
			// 			item.push(this.timeFormat( btn_data[i][0],'ymd'));
			// 		}else{
			// 			item.push(this.timeFormat( btn_data[i][0],'hm'));
			// 		}
					
			// 		//item.push(btn_data[i][0]);
			// 		item.push(btn_data[i][1]);
			// 		item.push(btn_data[i][4]);
			// 		item.push(btn_data[i][3]);
			// 		item.push(btn_data[i][2]);
			// 		item.push(btn_data[i][5]);
					
			// 		btn_data_tmp.push(item);
			// 	}

			// 	//
			// 	this.gotoecharts(btn_data_tmp);

			// }).catch(err =>{
				
			// 	this.utils.err('k...err...3s');

			// 	let getKDataTimeout = setTimeout(() =>{
			// 		this.get_k_data(flag);
			// 		clearTimeout(getKDataTimeout);	
			// 	},3000)

				
			// })
		},
		//k
		splitData(rawData) {
			var categoryData = [];
			var values = []
			for (var i = 0; i < rawData.length; i++) {
				categoryData.push(rawData[i].splice(0, 1)[0]);
				values.push(rawData[i])
			}
			return {
				categoryData: categoryData,
				values: values
			};
		},
		//
		calculateMA(data0,dayCount) {
			var result = [];
			for (var i = 0, len = data0.values.length; i < len; i++) {
				if (i < dayCount) {
					result.push('-');
					continue;
				}
				var sum = 0;
				for (var j = 0; j < dayCount; j++) {
					sum += data0.values[i - j][1];
				}
				result.push((sum / dayCount).toFixed(4));
			}
			return result;
		},
		kTl(KDS){
			var K2line=new Array();
			for(var p=0;p<KDS.length;p++){
				K2line.push(KDS[p][0])
				// if (p == KDS.length-2) {
				// 	K2line[p] = KDS[p][0];
				// }
				// if (p == KDS.length-1) {
				// 	K2line[p] = KDS[p][0];
				// }
			}
			//alert(JSON.stringify(K2line))
			return K2line;
		},
		//
		gotoecharts(data){
			var that = this;
			if(this.chartline){
				//this.chartline.clear();
				this.chartline.hideLoading();
			}else{
				this.chartline = echarts.init(this.$refs.echartsBox);
			}

			let data0 = this.splitData(data);

			//console.log('data0...',data0);

			//k
			let new_item = data0.values[data0.values.length -1];
			//console.log('new_item...',new_item);
			this.btn_k_data_item.open = new_item[0].toFixed(2);
			this.btn_k_data_item.close = new_item[1].toFixed(2);
			this.btn_k_data_item.lowest = new_item[2].toFixed(2);
			this.btn_k_data_item.highest = new_item[3].toFixed(2);

			//y
			let markLine_y = 0;
			if(this.btn_k_interval.sell == ''){
				markLine_y = new_item[0]
			}else{
				markLine_y = parseFloat(this.btn_k_interval.sell);
			}

			//console.log('data...',data);
			let k2linedata = this.kTl(data);
			// k2linedata.push(new_item[0]);
			//console.log('k2linedata...',k2linedata);

			var upColor = '#48be7b';
			var upBorderColor = '#48be7b';
			var downColor = '#ff4a4a';
			var downBorderColor = '#ff4a4a';

			let option = {
				tooltip: {
					show:true,
					trigger: 'axis',
					axisPointer: {
						type: 'cross',
						crossStyle:{
							color:'#555',
							type:'dashed',
						},
						
					},
					showContent:false
				},
				axisPointer:{
					show:true,
					lineStyle:{
						color:'#555',
						type:'dashed',
					},
					label:{
						show:true,
						precision:'2',
						margin:0,
						color:'#fff',
						backgroundColor:'#555',
						formatter: function (params) {
							
							//console.log('label...2',params)
							that.firstInAxisPointer = true;
							if(params.seriesData.length != 0){
								let data = params.seriesData[0].value;
								that.btn_k_data_item.open = data[1].toFixed(2);
								that.btn_k_data_item.close = data[2].toFixed(2);
								that.btn_k_data_item.lowest = data[3].toFixed(2);
								that.btn_k_data_item.highest = data[4].toFixed(2);

								return params.value
							}else{
								return params.value.toFixed(2)
							}
							

						}
					}
				},
				grid: {
					
					left: '15',
					right: '10',
					bottom: '15',
					top:'15',
					containLabel:true
				},
				xAxis: {
					type: 'category',
					data: data0.categoryData,	
					scale: true,
					boundaryGap : false,
					axisLine: {
						onZero: false,
						lineStyle: { color: '#39404f' }
					},
					axisTick:{//
						show:true,
					},
					axisLabel:{//
						show:true,
						color:'#afa799'
					},
					splitLine: {show: false},
					splitNumber: 30,
					min: 'dataMin',
					max: 'dataMax'
				},
				yAxis: {
					scale: true,
					position:'right',
					axisLine: {//
						onZero: false,
						lineStyle: { color: '#39404f' }	
					},
					axisTick:{//
						show:true,
					},
					axisLabel:{//
						show:true,
						color:'#afa799',
						formatter:function(value,index){
							return value.toFixed(2)
						}
					},
					splitLine:{//
						show:true,
						lineStyle:{
							color:'#292d38'
						}
					},
					
					
					
				},
				dataZoom: [
					{
						type: 'inside',
						start: 50,
						end: 100
					},
				],
				series: [
					{
						name: 'K',
						type: 'candlestick',
						data: data0.values,
						itemStyle: {
							normal: {
								color: upColor,
								color0: downColor,
								borderColor: upBorderColor,
								borderColor0: downBorderColor
							}
						},
						markLine: {
							data: [
								{yAxis:markLine_y}
							],
							symbol:'',
							lineStyle:{
								normal:{
									color:'#48be7b',
									type:'dashed',
								}
								
							},
							label:{
								// color:'#fff',
								// backgroundColor:'#555',
								normal:{
									formatter: function(params){
										//console.log('markLine....value',params);
										return params.value.toFixed(2);
									}
								}
							},
							animationDuration:0
						},
					
					},
					{
						name: 'MA5',
						type: 'line',
						data: this.calculateMA(data0,5),
						smooth: true,
						showSymbol: false,
						lineStyle: {
							normal: {width:1}
						},
						animationDuration:0,
						itemStyle:{
							normal:{
								opacity:0
							}
						}
					},
					{
						name: 'MA10',
						type: 'line',
						data: this.calculateMA(data0,10),
						showSymbol: false,
						lineStyle: {
							normal: {width:1}
						},
						animationDuration:0,
						itemStyle:{
							normal:{
								opacity:0
							}
						}
					},
					{
						name: 'MA20',
						type: 'line',
						data: this.calculateMA(data0,20),
						smooth: true,
						showSymbol: false,
						lineStyle: {
							normal: {width:1}
						},
						animationDuration:0,
						itemStyle:{
							normal:{
								opacity:0
							}
						}
					},
				]
			}
			let option1 = {
		
				grid: {
					
					left: '15',
					right: '10',
					bottom: '15',
					top:'15',
					containLabel:true
				},
				xAxis: {
					type: 'category',
					data: data0.categoryData,	
					scale: true,
					boundaryGap : false,
					axisLine: {
						onZero: false,
						lineStyle: { color: '#39404f' }
					},
					axisTick:{//
						show:true,
					},
					axisLabel:{//
						show:true,
						color:'#afa799'
					},
					splitLine: {show: false},
					splitNumber: 30,
					min: 'dataMin',
					max: 'dataMax'
				},
				yAxis: {
					scale: true,
					position:'right',
					axisLine: {//
						onZero: false,
						lineStyle: { color: '#39404f' }	
					},
					axisTick:{//
						show:true,
					},
					axisLabel:{//
						show:true,
						color:'#afa799',
						formatter:function(value,index){
							return value.toFixed(2)
						}
					},
					splitLine:{//
						show:true,
						lineStyle:{
							color:'#292d38'
						}
					},
					
					
					
				},
				dataZoom: [
					{
						type: 'inside',
						start: 50,
						end: 100
					},
				],
				series: [
					{
						name: 'K',
						type: 'line',
						//data: this.kTl(data),
						data: k2linedata,
						smooth:false,
						symbol: 'none',
						sampling: 'average',
						itemStyle: {
							normal: {
								color: 'rgb(255, 70, 131)'
							}
						},
						lineStyle:{
							normal:{
								width:2,
								color:"#d2c01e"
							}
						},
						areaStyle: {
							normal: {
								color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
									offset: 0,
									color: '#474019'
								}, {
									offset: 1,
									color: '#262922'
								}])
							}
						},
						animationDuration:0,
						markLine: {
							data: [
								{yAxis:markLine_y}
							],
							symbol:'',
							lineStyle:{
								normal:{
									color:'#48be7b',
									type:'dashed',
								}
								
							},
							label:{
								// color:'#fff',
								// backgroundColor:'#555',
								normal:{
									formatter: function(params){
										//console.log('markLine....value',params);
										return params.value.toFixed(2);
									}
								}
							},
							animationDuration:0
						},
						
					},
					
				]
			}

			
			if(this.typeIndex > 0){
				this.chartline.setOption(option);
			}else{
				this.chartline.setOption(option1);
			}

			
		},
		//
		get_price_data(){
			//let url = 'http://api.zb.cn/data/v1/ticker?market=btc_usdt';
			// let url = 'http://jf.com/api/wave/btc_now';
			this.http.post(this.http._HOST + 'Notoken/btc_now').then(result =>{
				
                //this.utils.info('...',result);
                if(result.success && result.data.ticker){

                
                    

                    if(this.chartline){

						this.btn_price_data = result.data.ticker;

						//
						this.sendToParent();
                        
                        let option = this.chartline.getOption();
                        //this.utils.info('chartline option...in get_price_data',option);
                        if(option && option.series[0] && option.series[0].markLine){
                            
                            let yAxis = parseFloat(this.btn_price_data.sell);
                            // console.log('yAxis...',yAxis)
                            // console.log('yAxis...2',yAxis.toFixed(2))
                            option.series[0].markLine.data[0].yAxis = yAxis;
                            

                            if(this.typeIndex == 0){
                                
                                
                                // console.log('yAxis...',yAxis);
                                // console.log('yAxis...111',option.series[0].data[option.series[0].data.length - 1]);
                                if(option.series[0].data[option.series[0].data.length - 1] <= yAxis){
                                    option.series[0].markLine.lineStyle.color = '#48be7b';
                                }else{
                                    option.series[0].markLine.lineStyle.color = '#ff4a4a';
                                }

                                option.series[0].data[option.series[0].data.length - 1] = yAxis;
                            }else{
                                
                                let open = option.series[0].data[option.series[0].data.length - 1][0];
                                let close = option.series[0].data[option.series[0].data.length - 1][1];
                                // console.log('open...',open);
                                // console.log('close...',close);
                                if(open <= close){
                                    option.series[0].markLine.lineStyle.color = '#48be7b';
                                }else{
                                    option.series[0].markLine.lineStyle.color = '#ff4a4a';
                                }

                                option.series[0].data[option.series[0].data.length - 1][1] = yAxis;	
                            }
                            
                            this.chartline.setOption(option);

                            if(!this.firstInAxisPointer){
                                this.btn_k_data_item.close = yAxis.toFixed(2);
                            }
                            
                            
                        }

                        
                    }
                }
				
			})
		},
		//
		timeFormat(timestamp,type){
			// let timestamp = parseInt(val) * 1000;
			let date = new Date(timestamp);//10*1000131000
			let year = date.getFullYear();
			let month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
			let day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
			let hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
			let minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
			let seconds = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
			
			if(type == 'hm'){
				return hour + ":" + minute;
			}
			if(type == 'ymd'){
				return year + "-" + month + "-" + day;
			}
			//return year + "-" + month + "-" + day + " " + hour + ":" + minute + ":" + seconds;
		}
    }
}
</script>

